@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700");
body {
  color: #4f4f4f;
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  visibility: visible;
}

img {
  max-width: 100%;
  height: auto;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Montserrat';
  color: #5b5b5b;
  font-weight: 400;
}

p:last-child {
  margin-bottom: 0;
}

a, button {
  text-decoration: none;
  color: inherit;
}

a, i, button, img, input, span {
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

*:before, *:after {
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

*:focus {
  outline: 0px solid;
}

a:focus {
  color: #4f4f4f;
  outline: medium none;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul {
  list-style: outside none none;
  margin: 0;
  padding: 0;
}

blockquote {
  border-left: 6px solid #2d3e50;
  margin-bottom: 40px;
  padding: 25px;
}

blockquote p {
  color: #ffffff;
  font-family: 'Montserrat';
  font-size: 14px;
  font-weight: 300;
  line-height: 23px;
}

