/* Section Title */
.section-title h2 {
  color: #4f4f4f;
  font-size: 30px;
  font-weight: normal;
  line-height: 1;
  margin: 0 0 25px;
  padding-bottom: 25px;
  position: relative;
  text-transform: capitalize;
}

.section-title h2:before, .section-title h2:after {
  content: "";
  height: 2px;
  position: absolute;
  background-color: #7c7c7c;
}

.section-title h2:before {
  bottom: 0;
  width: 70px;
}

.section-title.text-left h2:before {
  left: 0;
}

.section-title.text-center h2:before {
  left: 50%;
  margin-left: -35px;
}

.section-title h2:after {
  bottom: 5px;
  width: 40px;
}

.section-title.text-left h2:after {
  left: 0;
}

.section-title.text-center h2:after {
  left: 50%;
  margin-left: -20px;
}

/* Btn */
.btn {
  border: 1px solid transparent;
  border-radius: 50px;
  color: #fff;
  display: inline-block;
  font-size: 13px;
  font-weight: bold;
  height: 50px;
  letter-spacing: 1px;
  line-height: 52px;
  padding: 0 47px;
  position: relative;
  text-transform: uppercase;
  z-index: 1;
}

.btn:before {
  border-radius: 50px;
  bottom: -1px;
  content: "";
  display: block;
  left: -1px;
  position: absolute;
  right: -1px;
  top: -1px;
  -webkit-transform: scale(0);
  -ms-transform: scale(0);
  transform: scale(0);
  z-index: -1;
}

.btn:hover:before {
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
}

.btn.transparent {
  background-color: transparent;
}

.btn.black {
  border-color: #2d3e50;
  color: #2d3e50;
}

.btn.hover-white:hover:before {
  background-color: #fff;
}

.btn.hover-black:hover {
  color: #fff;
}

.btn.hover-black:hover:before {
  background-color: #2d3e50;
}

/* Input Placeholder */
input:-moz-placeholder, textarea:-moz-placeholder, input::-webkit-input-placeholder, textarea::-webkit-input-placeholder, input::-moz-placeholder, textarea::-moz-placeholder, input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  opacity: 1;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}

/* Parallax Css */
.parallax-window {
  background: transparent;
}

.parallax-slider, .backstretch img {
  -webkit-transition: none 0s ease 0s;
  transition: none 0s ease 0s;
}

/* Slideshow BG Css */
/* Kenburnsy Slideshow */
.kenburnsy {
  overflow: hidden;
  position: absolute;
  width: 100%;
}

.kenburnsy.fullscreen {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -9;
}

.kenburnsy img {
  display: none;
}

.kenburnsy .slide {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-transform-origin: center;
  -ms-transform-origin: center;
  transform-origin: center;
}

/* Hero Video Overlay Css */
.video-container {
  position: relative;
}

.video-container:before {
  background-color: #000;
  bottom: 0;
  content: "";
  left: 0;
  opacity: 0.6;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=60)";
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
}

/* Hero Youtube Css */
#controlBar_hero.mb_YTPBar {
  display: none !important;
}

.mbYTP_wrapper {
  z-index: -2 !important;
}

.youtube-bg:before, .vimeo-bg:before {
  background-color: #000;
  bottom: 0;
  content: "";
  left: 0;
  opacity: 0.6;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=60)";
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
}

/* Box Layout */
.boxed-layout .layout-wrapper {
  box-shadow: 0 0 7px 3px rgba(0, 0, 0, 0.1);
  float: none;
  margin: 0 auto;
  max-width: 1200px;
}

