/*  2.  Header Area
/*----------------------------------------*/
#header-area {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 99999;
  top: 0;
}

/* Header Top */
.header-top-wrapper-1 ul {
  float: right;
  padding: 10px 0;
}

.header-top-wrapper-1 ul li {
  color: #d9d9d9;
  display: block;
  float: left;
  font-family: 'Montserrat';
  font-weight: 300;
  line-height: 30px;
}

.header-top-wrapper-1 ul li + li {
  margin-left: 36px;
}

.header-top-wrapper-1 ul li span {
  display: block;
  float: left;
  margin-right: 5px;
}

.header-top-wrapper-1 ul li p {
  display: block;
  float: left;
  overflow: hidden;
}

.header-top-wrapper-1 ul li a {
  display: block;
  float: left;
  overflow: hidden;
}

/* Header Bottom */
.header-bottom-1.stick {
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  background-color: #44b272;
}

.header-bottom-wrapper-1 {
  padding-bottom: 40px;
  padding-top: 20px;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.stick .header-bottom-wrapper-1 {
  padding-bottom: 10px;
  padding-top: 20px;
}

.stick .header-bottom-wrapper-1 .logo {
  padding: 18px 45px;
  margin-top: 20px;
  top: -20px;
}

.stick .header-bottom-wrapper-1 .logo .logo-image {
  background: url("../../images/logo-white.png") no-repeat;
  background-size: 75%;
}

@media only screen and (max-width: 992px) {
  .stick .header-bottom-wrapper-1 .logo .logo-image {
    background-size: 50%;
  }
}

.stick .menu-phone {
  color: #ffffff;
}

.header-bottom-wrapper-1 .logo {
  bottom: 0;
  display: block;
  left: 0;
  padding: 58px 47px;
  position: absolute;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  z-index: 9999999;
  top: -40px;
}

.header-bottom-wrapper-1 .logo .logo-image {
  width: 180px;
  height: 67px;
  background: url("../../images/logo.png") no-repeat;
}

@media only screen and (max-width: 992px) {
  .header-bottom-wrapper-1 .logo .logo-image {
    background-size: 50%;
  }
}

/* Menu */
.main-menu {
  margin-right: 20px;
}

.main-menu nav ul li {
  display: block;
  float: left;
  padding: 2px 0;
  color: #fff;
}

.main-menu nav ul li + li {
  margin-left: 36px;
}

.main-menu nav ul li a {
  color: #fff;
  display: block;
  font-family: 'Montserrat';
  font-weight: normal;
  line-height: 26px;
  position: relative;
  text-transform: uppercase;
}

.main-menu nav ul li a:before, .main-menu nav ul li a:after {
  background-color: #fff;
  content: "";
  height: 2px;
  opacity: 0;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  position: absolute;
  width: 100%;
}

.main-menu nav ul li a:before {
  left: 0;
  top: 0;
}

.main-menu nav ul li a:after {
  bottom: 0;
  right: 0;
}

.main-menu nav ul li a:hover:before, .main-menu nav ul li a:hover:after {
  opacity: 1;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
  width: 14px;
}

.main-menu nav ul li.active a:before, .main-menu nav ul li.active a:after {
  opacity: 1;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
  width: 14px;
}

/* Search Button */
.header-search {
  background-color: transparent;
  border: medium none;
  color: #fff;
  display: block;
  margin-left: 20px;
  margin-right: 5px;
  padding: 0 10px;
}

.header-search i {
  display: block;
  font-size: 20px;
  line-height: 30px;
