/*-- Search Area --*/
.search-area {
  background-color: #fff;
  left: 0;
  padding: 30px;
  position: fixed;
  right: 0;
  top: 100%;
  -webkit-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s;
