}

.search-area.active {
  top: 0;
}

.search-form {
  position: relative;
}

.search-form .search-close {
  background-color: transparent;
  border: medium none;
  left: 50%;
  margin-left: -20px;
  opacity: 0.5;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
