/*----------------------------------------*/
.hero-area-1 {
  position: relative;
}

/* Hero Flex Slider */
.hero-slider-1 .single-slide {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.hero-slider-1 .single-slide .content {
  margin-top: 50px;
  -webkit-animation-name: fadeOutDown;
  animation-name: fadeOutDown;
}

.hero-slider-1 .flex-active-slide .content {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
}

.hero-slider-1 .single-slide .content h1 {
  color: #ffffff;
  font-size: 60px;
  font-weight: 600;
  line-height: 1;
  margin: 0 0 15px;
}

.hero-slider-1 .single-slide .content p {
  color: #ffffff;
  font-size: 18px;
  font-weight: 300;
  line-height: 32px;
  margin-bottom: 35px;
}

/* Hero Flex Control */
.hs1-control {
  bottom: 110px;
  left: 0;
  position: absolute;
  right: 0;
  z-index: 99;
}

.hs1-control .flex-control-nav {
  float: left;
  margin: 0 0 0 -65px;
  padding: 0;
  position: relative;
}

.hs1-control .flex-control-nav li {
  display: block;
  float: left;
  margin-right: 11px;
  position: relative;
}

.hs1-control .flex-control-nav li:last-child {
  margin-right: 0;
}

.hs1-control .flex-control-nav li:before {
  background-color: #fff;
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  position: absolute;
  right: -10px;
  width: 11px;
}

.hs1-control .flex-control-nav li:last-child:before {
  display: none;
}

.hs1-control .flex-control-nav li a {
  color: #fff;
  float: left;
  font-family: 'Montserrat';
  font-size: 20px;
  font-weight: 600;
  line-height: 14px;
  padding: 0 8px;
}

/* Hero Social */
